import { APIGatewayEvent, APIGatewayProxyResult } from 'aws-lambda';
import { MongoClient } from 'mongodb';
import { ObjectID } from "bson";
import { find, findOne } from './crud.js';
import { generateErrorResponse } from './utility.js'

let db: any = null;

export const handler = async (event: APIGatewayEvent): Promise<APIGatewayProxyResult> => {
    if (event.path === undefined || event.path === null) {
        return generateErrorResponse( 200, "node.js rest api - read optimized - connect an api gateway to access the resources");
    }

    const [emp, api, eRoute, entity, entityId] = event.path.split("/");
    if ((emp + "/" + api + "/" + eRoute) != "/api/e") {
        return generateErrorResponse(404, "not found - invalid e-route");
    }

    if (entity === undefined || entity.trim().length === 0) {
        return generateErrorResponse( 404, "not found - invalid e-route - entity missing");
    }

    const queryParameters = event.queryStringParameters ?? {};
    const limit: number = parseInt(queryParameters.limit ?? "10") || 10;
    const page: number = parseInt(queryParameters.page ?? "1") || 1;

    try {
        if (db == null) {
            const uri = process.env.MONGODB_URI;
            if (uri === undefined || uri === null || uri.trim().length === 0) {
                return generateErrorResponse( 500, "missing database connection string: mongodb uri - please provide an Environment Variable in AWS Lambda for MONGODB_URI - How to configure?: https://docs.aws.amazon.com/lambda/latest/dg/configuration-envvars.html");
            }
            const client = new MongoClient(uri);
            await client.connect();
            const dbName = process.env.MONGODB_DATABASE_NAME
            if (dbName === undefined || dbName === null || dbName.trim().length === 0) {
                return generateErrorResponse( 500, "missing, mongodb database name - please provide an Environment Variable in AWS Lambda for MONGODB_DATABASE_NAME - How to configure?: https://docs.aws.amazon.com/lambda/latest/dg/configuration-envvars.html");
            }
            db = client.db(dbName);
        }

        const bodyJson = JSON.parse(event.body || "{}");
        const { filter = {}, sort = { _id: -1 }, fields = [] } = bodyJson;
        let result = null;
        if (entityId !== undefined && entityId != null) {
            if (entityId.trim().length === 0) {
                return generateErrorResponse( 404, "not found - invalid e-route, entityId missing");
            }
            else {
                const query = { _id: new ObjectID(entityId) };
                result = await findOne(db, entity || '', query, fields);
            }
        }
        else {
            result = await find(db, entity || '', filter, sort, page, limit, fields);
        }

        let response: APIGatewayProxyResult = {
            statusCode: 200,
            headers: {
                "Content-Type": "application/json",
                "Access-Control-Allow-Origin": "*",
                "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept, Authorization",
                "Access-Control-Allow-Methods": "OPTIONS,GET,PUT,POST,DELETE"
            },
            body: JSON.stringify(result),
        }
        return response

    } catch (error: any) {
        return generateErrorResponse( 500, error.message);
    }
};

